import React, { useEffect, useState } from "react";
import APIService from "../servers/APIService";
import style from "../styles/dashboard.module.less";
export default function Dashboard() {
    const [userInfo, setUserInfo] = useState({});

    useEffect(() => {
        APIService.fetchUserInfo().then((res) => {
            if (res && res.success === 1) {
                setUserInfo(res.userInfo);
            }
        });
    }, []);

    return (
        <>
            <div className="col-xl-4 col-lg-6 col-md-6 col-sm-6 col-12 layout-spacing">
                <div className={style["api-view"]}>
                    <div className={style["title"]}>API KEY</div>
                    <div className={style["item-view"]}>
                        <i data-feather="key"></i>
                        <span className={style["label-title"]}>API Key</span>
                        <span>{userInfo.apiKey}</span>
                    </div>
                    <div className={style["line"]}></div>
                    <div className={style["item-view"]}>
                        <i data-feather="key"></i>
                        <span className={style["label-title"]}>API Secret</span>
                        <span>{userInfo.apiSecret}</span>

                        <i data-feather="eye" style={{ marginLeft: 5 }}></i>
                    </div>
                </div>
            </div>

            <div className="col-xl-3 col-lg-5 col-md-5 col-sm-6 col-12 layout-spacing">
                <div className={style["info-view"]}>
                    <div className={style["main-title"]}>INFORMATION</div>
                    <div className={style["bottom-view"]}>
                        <div className={style["sub-view"]}>
                            <div className={style["title"]}>Announcement</div>
                            <div className={style["num"]}>5</div>
                            <div className={style["check-btn"]}>Check</div>
                        </div>
                        <div className={style["sub-view"]}>
                            <div className={style["title"]}>Message</div>
                            <div className={style["num"]}>3</div>
                            <div className={style["check-btn"]}>Check</div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="col-xl-5 col-lg-7 col-md-7 col-sm-6 col-12 layout-spacing">
                <div className={style["account-view"]}>
                    <div className={style["top-view"]}>
                        <i className={style["head-view"]} />
                        <div style={{ flex: 1 }}></div>
                        <div className={style["detail-view"]}>
                            <div className={style["name-view"]}>
                                <i data-feather="edit-3"></i>
                                <span>{userInfo.userName}</span>
                            </div>
                            <div>{`Email:${userInfo.mail}`}</div>
                            <div>{`Tel:${userInfo.tel}`}</div>
                        </div>
                    </div>
                    <div className={style["bottom-view"]}>
                        <div className={style["sub-view"]}>
                            <div className={style["title"]}>API:</div>
                            <div className={style["num"]}>2</div>
                        </div>
                        <div className={style["sub-view"]}>
                            <div className={style["title"]}>BALANCE:</div>

                            <div className={style["balance-view"]}>
                                {userInfo.balance}
                                <span>JPY</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}
